@font-face {
  font-family: '2049';
  src: url('fonts/OT2049-Regular.otf') format('truetype');
}

@font-face {
  font-family: 'Road Rage';
  src: url('fonts/Road_Rage.otf') format('opentype');
}

@font-face {
  font-family: 'Cherish Regular';
  src: url('fonts/Cherish-Regular.ttf') format('truetype');
}

:root {
  --main-text: #ffcc66;
  --shadow-color: rgba(0, 123, 255, 0.5);
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--main-text) #2e2c3c;
}

body {
  display: grid;
  overflow-x: hidden;
}

.flex {
  display: flex;
}

.rage {
  font-family: 'Road Rage';
  text-shadow: 0 0 0.2rem #ffcc66, 0 0 0.5rem #ff9933;
  box-sizing: border-box;
  background: transparent;
  color: var(--main-text);
  margin-left: 10px;
}

.grid {
  display: grid;
  gap: 1.5rem;
  grid-auto-columns: 1fr;
  grid-template-areas: 'one' 'two' 'three' 'four' 'five' 'six' 'seven' 'eight' 'nine';
  padding-block: 2rem;
  width: min(95%, 70rem);
  margin-inline: auto;
  position: relative;
  z-index: 1;
}

.grid-item {
  border-radius: 0.5rem;
}

/* Original media queries for grid layout */
@media screen and (min-width: 16em) {
  .grid {
    grid-template-areas:
      "one"
      "two"
      "three"
      "six"
      "four"
      "five"
      "eight"
      "eight"
      "nine";
  }
}

@media screen and (min-width: 20em) {
  .grid {
    grid-template-areas:
      "one one"
      "two two"
      "six six"
      "four five"
      "eight eight"
      "nine nine";
  }
}

@media screen and (min-width: 38em) {
  .grid {
    grid-template-areas:
      "one one"
      "two two"
      "six six"
      "four five"
      "eight eight"
      "nine nine";
  }
}

@media screen and (min-width: 54em) {
  .grid {
    grid-template-areas:
      "one one one"
      "two two three"
      "six six six"
      "four seven five"
      "nine eight eight";
  }
}

@media screen and (min-width: 75em) {
  .grid {
    grid-template-areas:
      "one one one"
      "two two three three"
      "six six six six"
      "four four five five"
      "nine nine eight eight";
  }
}

/* Added responsive media queries for grid padding and width adjustments */
@media (min-width: 576px) {
  .grid {
    padding-block: 2.5rem;
    /* Adjust the grid-template-areas if necessary */
  }
}

@media (min-width: 768px) {
  .grid {
    padding-block: 3rem;
    width: min(90%, 70rem);
    /* Adjust the grid-template-areas if necessary */
  }
}

@media (min-width: 992px) {
  .grid {
    padding-block: 3.5rem;
    width: min(85%, 70rem);
    /* Adjust the grid-template-areas if necessary */
  }
}

@media (min-width: 1200px) {
  .grid {
    padding-block: 4rem;
    width: min(80%, 70rem);
    /* Adjust the grid-template-areas if necessary */
  }
}

@media (min-width: 1400px) {
  .grid {
    padding-block: 4.5rem;
    width: min(75%, 70rem);
    /* Adjust the grid-template-areas for very large screens */
  }
}
