.header {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-area: one;
    color: #FFCC66;
    font-family: 'Road Rage', sans-serif;
    font-size: 2em;
    text-shadow: 
      0 0 0.2rem #FFCC66, 
      0 0 0.5rem #FF9933, 
      0 0 0.5rem #FF6600, 
      0 0 0.8rem #CC5500; 
    box-sizing: border-box;
    background: transparent;
    
}

@media screen and (min-width: 16em) {
  .header{
    font-size: 1em;
  }
}



@media screen and (min-width: 20em) {
  .header{
    font-size: 1em;
  }
}

@media screen and (min-width: 38em) {
  .header{
    font-size: 1.5em;
  }
}

@media screen and (min-width: 54em) {
  @media screen and (min-width: 38em) {
    .header{
      font-size: 1.8em;
    }
  }
}

