.leader {
  font-family: '2049';
  grid-area: two;
  font-size: 1.6em;
  background: none;
  color: var(--main-text);
  padding: 1em;
  text-transform: uppercase;
  box-shadow: 
  0 0 40px 10px var(--shadow-color);
}

@media screen and (min-width: 16em) {
  .leader{
    font-size: 1em;
  }
}



@media screen and (min-width: 20em) {
  .leader{
    font-size: 1em;
  }
}

@media screen and (min-width: 38em) {
  .leader{
    font-size: 1.5em;
  }
}

@media screen and (min-width: 54em) {
  .leader{
    font-size: 1.6em;
  }
}

